import { useState, useEffect, React } from "react";
import {
  useNavigate,
  BrowserRouter as Router,
  Route,
  Routes,
} from "react-router-dom";
import Menu from "./components/app/Menu";
import List from "./components/page/List";
import Settings from "./components/page/Settings";
import Create from "./components/page/Create";
import Analyse from "./components/page/Analyse";
import "./app.css";
function App() {
  const [expenses, setExpenses] = useState([]);
  useEffect(() => {
    fetchExpenses();
  }, []);

  const fetchExpenses = async () => {
    try {
      const response = await fetch("http://localhost:8000/api/all-expenses/", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      });

      if (response.ok) {
        const data = await response.json();
        setExpenses(data);
      } else {
        console.error(
          "Error fetching expenses:",
          response.status,
          response.statusText
        );
      }
    } catch (error) {
      console.error("Error fetching expenses:", error);
    }
  };

  const useCustomNavigation = () => {
    const navigate = useNavigate();

    const goToMenu = () => {
      navigate("/");
    };

    // Add more navigation functions if needed

    return { goToMenu };
  };
  return (
    <Router basename="/finass">
      <div className="App">
        <header>
          <div>
            <h1>FIN ASS</h1>
          </div>
        </header>
        <main>
          <Routes>
            <Route path="/" element={<Menu />} />
            <Route
              path="/list-expenses"
              element={
                <List
                  expenses={expenses}
                  setExpenses={setExpenses}
                  useCustomNavigation={useCustomNavigation}
                  fetchExpenses={fetchExpenses}
                />
              }
            />
            <Route
              path="/create-expense"
              element={<Create useCustomNavigation={useCustomNavigation} />}
            />
            <Route
              path="/analysis"
              element={
                <Analyse
                  expenses={expenses}
                  useCustomNavigation={useCustomNavigation}
                />
              }
            />
            <Route
              path="/settings"
              element={
                <Settings
                  currentExpenses={expenses}
                  useCustomNavigation={useCustomNavigation}
                />
              }
            />
          </Routes>
        </main>
        <footer></footer>
      </div>
    </Router>
  );
}

export default App;
